/* work-sans-300 - latin */
@font-face {
  font-family: 'Work Sans';
  font-style: normal;
  font-weight: 300;
  src: url('/wp-content/themes/gyro/assets/fonts/work-sans-v3-latin-300.eot'); /* IE9 Compat Modes */
  src: local('Work Sans Light'), local('WorkSans-Light'),
  url('/wp-content/themes/gyro/assets/fonts/work-sans-v3-latin-300.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
  url('/wp-content/themes/gyro/assets/fonts/work-sans-v3-latin-300.woff2') format('woff2'), /* Super Modern Browsers */
  url('/wp-content/themes/gyro/assets/fonts/work-sans-v3-latin-300.woff') format('woff'), /* Modern Browsers */
  url('/wp-content/themes/gyro/assets/fonts/work-sans-v3-latin-300.ttf') format('truetype'), /* Safari, Android, iOS */
  url('/wp-content/themes/gyro/assets/fonts/work-sans-v3-latin-300.svg#WorkSans') format('svg'); /* Legacy iOS */
}
